import React from "react";
import PropTypes from "prop-types";
import { StyledControl, StyledButton } from "./Controls.styles";

const Controls = ({ handleClick, index, length }) => (
  <StyledControl>
    <StyledButton
      type="button"
      name="back"
      disabled={!index}
      onClick={handleClick}
    >
      Назад
    </StyledButton>

    <StyledButton
      type="button"
      name="ahead"
      disabled={index + 1 === length}
      onClick={handleClick}
    >
      Вперед
    </StyledButton>
  </StyledControl>
);

Controls.propTypes = {
  handleClick: PropTypes.func.isRequired,
  index: PropTypes.number.isRequired,
  length: PropTypes.number.isRequired
};

export default Controls;
